html, body{
    color: white;
    background-color: #21232A;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.3rem;
    padding: 30px;
}
button{
    font-size: 1.3rem;
    margin-right: 10px;
    padding: 10px;
}
.card{
    align-content: center;
    border: 1px solid white;
    overflow: hidden;
    margin: 10px;
    border-radius: 6px;

}
.card p{
    padding: 5px;
    text-align: center;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}
.card img {
    max-height: 170px;    
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}